<template>
  <div>
    <div style="margin-left: 20px">
      <div>
        Select Robot
      </div>
      <a-row>
        <a-col :span="10">
          <a-select style="width: 150px" :defaultValue="1">
            <a-select-option
              v-for="robot in robotList"
              :key="robot.id"
              :value="robot.id"
            >
              {{ robot.name }}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>
    </div>

    <div style="margin-left: 20px">
      <div style="margin-top: 30px">
        Current Section
      </div>
      <a-row>
        <a-col :span="10">
          <a-select style="width: 150px" :defaultValue="1">
            <a-select-option
              v-for="section in sectionList"
              :key="section.id"
              :value="section.id"
            >
              {{ section.name }}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>
    </div>

    <div style="margin-left: 20px">
      <div style="margin-top: 30px">
        Its heading
      </div>
      <a-row>
        <a-col :span="10">
          <a-select style="width: 150px" :defaultValue="1">
            <a-select-option
              v-for="direction in directionList"
              :key="direction.id"
              :value="direction.id"
            >
              {{ direction.name }}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>
    </div>

    <a-row style="margin-top: 30px">
      <a-button
        :type="mode == 'Auto' ? 'primary' : ''"
        @click="handleAutoModeBtnClick"
      >
        Auto Mode
      </a-button>
      <a-button
        :type="mode == 'Manual' ? 'primary' : ''"
        @click="handleManualModeBtnClick"
        style="margin-left: 5px"
      >
        Manual Mode
      </a-button>
    </a-row>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      robotList: [
        {
          name: "Robot 1",
          id: 1,
        },
        {
          name: "Robot 2",
          id: 2,
        },
      ],
      sectionList: [
        {
          name: "Available",
          id: 1,
        },
        {
          name: "Unavailable",
          id: 2,
        },
      ],
      directionList: [
        {
          name: "North",
          id: 1,
        },
        {
          name: "South",
          id: 2,
        },
        {
          name: "West",
          id: 3,
        },
        {
          name: "East",
          id: 4,
        },
      ],
      mode: "",
    };
  },
  methods: {
    handleAutoModeBtnClick() {
      this.mode = "Auto";
    },
    handleManualModeBtnClick() {
      this.mode = "Manual";
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>
